$Blue: hsl(246, 80%, 60%) ;
$Lightred: hsl(15, 100%, 70%) ;
$Softblue: hsl(195, 74%, 62%) ;
$Lightred1: hsl(348, 100%, 68%)  ;
$Limegreen: hsl(145, 58%, 55%) ;
$Violet: hsl(264, 64%, 52%) ;
$Softorange: hsl(43, 84%, 65%)  ;
$Verydarkblue: hsl(226, 43%, 10%);
$Darkblue: hsl(235, 46%, 20%);
$Desaturatedblue: hsl(235, 45%, 61%);
$PaleBlue: hsl(236, 100%, 87%);
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
}
body {
    max-height: 100vh;
    overflow: hidden;
    font-family: 'Rubik', sans-serif;
    color: white;
    .main {
        
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $Verydarkblue;
        .profile {
            position: relative;
            .profile-infos {
                background-color: $Blue;
                padding: 30px;
                height: 260px;
                width: 180px;
                border-radius: 15px;
                z-index: 1;
                position: absolute;
                top: -100%;
                left: 0;
                p {
                    margin-top: 10px;
                    font-weight: 300;
                    color: $PaleBlue;
                    font-size: 0.9em;
                }
                h1 {
                    font-weight: 300;
                    margin-top: 5px;
                }
                img {
                    border: 3px solid $PaleBlue;
                    border-radius: 50%;
                    width: 40%;
                }
            }
            .time-selection {
               transform: translateY(90%);
                width: 180px;          
                z-index: 0;
                background-color: $Darkblue;
                padding: 15px;
                border-radius: 15px;
                li {
                    color: $PaleBlue;
                    margin: 10px;
                    cursor: pointer;
                    transition: 0.3s ease;
                    &:hover {
                        color: white;
                    }
                    &.active {
                        color: white;
                    }
                }
            }
        }

        .cards {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        margin-left: 10px;
        .card {
            margin: 25px 10px;
            background: $Darkblue;
            padding:   20px;
            position: relative;
            width: 180px;
            border-radius: 15px;
            height: 150px;
            z-index: 100;
            h2 {
                margin-top: 20px;
                font-weight: 300;
                font-size: 2.3em;
            }
            h3 {
                font-size: 0.9em;
                font-weight: 500;
            }
            h4 {
                font-size: 0.7em;
                color: $PaleBlue;
                font-weight: 300;
                margin-top: 5px;
            }
            #dots {
                position: absolute;
                top: 5%;
                right: 10%;
            }
            &:nth-child(1) {
                &::before {
                    content: '';
                    position: absolute;
                    background: url("../public/images/icon-work.svg") center/cover no-repeat;
                    width: 40px;
                    height: 29px;
                    top: -20%;
                    right: 10%;
                    z-index: 1;
                }
               .logo {
                background-color: $Lightred;
                width: 180px;
                height: 30px;
                position: absolute;
                border-top-left-radius: 15px;
                border-top-right-radius: 15px;
                top: -20%;
                left: 0;
                z-index: 0;
                &::before {
                        content: "";
                        position: absolute;
                
                        background-color: transparent;
                        bottom: -50px;
                        height: 50px;
                        width: 25px;
                        border-top-left-radius: 15px;
                        box-shadow: 0 -25px 0 0 $Lightred;
                    }
                  &::after {
                       content: "";
                       position: absolute;
                     background-color: transparent;
                     bottom: -50px;
                     right: 0;
                     height: 50px;
                     width: 25px;
                     border-top-right-radius: 15px;
                     box-shadow: 0 -25px 0 0 $Lightred;
                                         }
               }
              
            }
                         &:nth-child(2) {
                             &::before {
                                 content: '';
                                 position: absolute;
                                 background: url("../public/images/icon-play.svg") center/cover no-repeat;
                                 width: 40px;
                                 height: 29px;
                                 top: -20%;
                                 right: 10%;
                                 z-index: 1;
                             }
            
                             .logo {
                                 background-color: $Softblue;
                                 width: 180px;
                                 height: 30px;
                                 position: absolute;
                                 border-top-left-radius: 15px;
                                 border-top-right-radius: 15px;
                                 top: -20%;
                                 left: 0;
                                 z-index: 0;
            
                                 &::before {
