                                                              width: 25px;
                                                              border-top-right-radius: 15px;
                                                              box-shadow: 0 -25px 0 0 $Lightred1;
                                                          }
                                                      }
                        
                                                  }
                 &:nth-child(4) {
                     &::before {
                         content: '';
